  transform: translateY(-0.5rem);
  box-shadow: 0 2rem 1rem -1rem rgba(24, 19, 7, 0.1);
}

/* ### Icon Examples */
.fa {
  text-shadow: 0.1em 0.1em 0 rgba(84, 77, 62, 0.1);
}

.icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.icons span {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 16px;
  font-size: 1.6rem;
  margin-bottom: 20px;
  margin-bottom: 2rem;
  color: #544d3e;
}

@media (min-width: 32em) {
  .icons span {
    width: 50%;
  }
}

@media (min-width: 56em) {
  .icons span {
    width: 33.33333333333%;
  }
}

@media (min-width: 80em) {
  .icons span {
    width: 25%;
  }
}

.icons i {
  font-size: 30px;
  font-size: 3rem;
  margin-right: 0.3em;
  color: #181307;
}

.icons br {
  display: none;
}

/* ### Pricing Table */
.pricing .site-inner {
  background-color: #cecece;
}

.pricing .entry {
  background-color: transparent;
}

.pricing-table {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 100px;
}

@media (min-width: 48em) {
  .pricing-table {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 60px;
  }
}

.pricing-table > div {
  background-color: #ffffff;
  padding: 40px 20px 50px;
  transition: all .3s ease;
  box-shadow: 0 0 0 rgba(24, 19, 7, 0);
  margin-bottom: 25px;
}

@media (min-width: 48em) {
  .pricing-table > div {
    margin: 0;
  }
}

.pricing-table > div:hover {
  transform: translateY(-1rem);
  z-index: 2;
  box-shadow: 0 2rem 2rem -0.5rem rgba(24, 19, 7, 0.1);
}

.pricing-table .featured {
  z-index: 1;
  position: relative;
  transform: scale(1.05);
}

.pricing-table .featured:hover {
  transform: translateY(-1rem) scale(1.05);
}

.pricing-table .featured button,
.pricing-table .featured .button {
  background-color: #b79449;
}

.pricing-table strong {
  font-weight: 300;
  color: #544d3e;
  font-size: 20px;
  font-size: 2rem;
}

.pricing-table span {
  font-style: italic;
  font-size: 13px;
  font-size: 1.3rem;
}

.pricing-table small {
  position: absolute;
  margin-top: 10px;
  margin-left: -10px;
}

.pricing-table big {
  font-size: 1.618em;
  color: #181307;
  font-weight: 300;
  font-family: "Muli", sans-serif;
}

.pricing-table p {
  margin: 0;
}

.pricing-table ul {
  margin: 10px 0 20px;
}

.pricing-table ul li {
  margin: 0;
  list-style-position: inside;
  list-style-type: none;
}

/*
 * IE specific styles.
 *
 * @link http://keithclark.co.uk/articles/moving-ie-specific-css-into-media-blocks/
 */
@media screen\0 {
  .nav-primary {
    margin: 0 0 0 auto;
  }
  .header-widget-area {
    margin-right: auto;
  }
}

/* # Print Styles
--------------------------------------------- */
@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    box-shadow: none !important;
    color: #181307 !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  a[href^="javascript:"]:after,
  a[href^="#"]:after,
  .site-title > a:after {
    content: "";
  }
  thead {
    display: table-header-group;
  }
  img,
  tr {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  @page {
    margin: 2cm 0.5cm;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  blockquote,
  pre {
    border: 2px solid #cecece;
    page-break-inside: avoid;
  }
  .content,
  .content-sidebar {
    width: 100%;
  }
  button,
  input,
  select,
  textarea,
  .breadcrumb,
  .comment-edit-link,
  .comment-form,
  .comment-list .reply a,
  .comment-reply-title,
  .edit-link,
  .entry-comments-link,
  .entry-footer,
  .genesis-box,
  .header-widget-area,
  .hidden-print,
  .home-top,
  .nav-primary,
  .nav-secondary,
  .post-edit-link,
  .sidebar {
    display: none !important;
  }
  .title-area {
    text-align: center;
    width: 100%;
  }
  .site-title > a {
    margin: 0;
    text-decoration: none;
    text-indent: 0;
  }
  .site-inner {
    padding-top: 0;
    position: relative;
    top: -10rem;
  }
  .author-box {
    margin-bottom: 0;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    orphans: 3;
    page-break-after: avoid;
    page-break-inside: avoid;
    widows: 3;
  }
  img {
    page-break-after: avoid;
    page-break-inside: avoid;
  }
  blockquote,
  pre,
  table {
    page-break-inside: avoid;
  }
  dl,
  ol,
  ul {
    page-break-before: avoid;
  }
}
